@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;1,300;1,400&display=swap);
body {
  height: 100vh;
  margin: 0;
}

.grid-container-a {
  color: #b1b0b0;
  text-decoration: none;
}

.footer-typography {
  opacity: 0.5;
}

.typography-container {
  font-family: 'Poppins', sans-serif !important;
}

.typography-span {
  color: #777;
}

.Button {
  color: aquamarine;
  background-color: aquamarine;
}

@media only screen and (min-width:768px) {
  .main_logo {
    margin-bottom: -30px;
  } 
}

@media (max-width: 440px) {
  .general {
    height: auto;
  }
  .typography-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    text-align: center;
  }
  .footer-container {
    margin-top: 24px;
  }
}

